<template>
  <section>
    <el-form :model="actInfos" :rules="rules" ref="actInfos" label-width="80px">
      <h2>活动基础信息</h2>
      <el-form-item label="活动名称" prop="actName">
        <el-input placeholder="如： 0601儿童节专场" style="width:400px" v-model="actInfos.actName"></el-input>
        <span class="warn">必填10个汉字以内，后台记录该活动名称，历史名称唯一</span>
      </el-form-item>
      <el-form-item label="活动描述" prop="actDesc">
        <el-input placeholder="如： 女王节满减/折扣活动" style="width:400px" v-model="actInfos.actDesc"></el-input>
        <span class="warn">选填20个汉字以内，描述该活动内容，方便管理记录</span>
      </el-form-item>
      <el-form-item label="活动时间" required>
        <el-col :span="4">
          <el-form-item prop="startTime">
            <el-date-picker
              type="datetime"
              placeholder="选择开始日期"
              v-model="actInfos.startTime"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="1" style="text-align: center;">-</el-col>
        <el-col :span="4">
          <el-form-item prop="endTime">
            <el-date-picker
              type="datetime"
              placeholder="选择结束时间"
              v-model="actInfos.endTime"
              style="width: 100%;"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8" class="line">
          <span class="warn">结束时间留空，则长期有效</span>
        </el-col>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'FillinActInfo',
  data() {
    return {
      rules: {
        actName: {
          required: true,
          message: '请输入活动名称',
          trigger: 'blur'
        },
        actDesc: {
          required: true,
          message: '请输入活动描述',
          trigger: 'blur'
        },
        startTime: {
          type: 'date',
          required: true,
          message: '请选择活动开始时间',
          trigger: 'change'
        }
        /* endTime: {
          type: 'date',
          // required: true,
          message: '请选择活动结束时间',
          trigger: 'change'
        } */
      }
    }
  },
  watch: {
    actInfos: {
      handler() {
        this.$refs.actInfos.validate(valid => {
          if (valid) {
            if (this.actInfos.endTime) {
              if (this.actInfos.endTime.getTime() < this.actInfos.startTime.getTime()) {
                this.$alert('结束时间不能早于开始时间', {
                  confirmButtonText: '确定',
                  callback: () => {
                    this.actInfos.endTime = ''
                  }
                })
                return
              }
            }
            this.actInfoFilled(true)
            this.setActivityInfos(this.actInfos)
          } else {
            this.actInfoFilled(false)
          }
        })
      },
      deep: true
    }
  },
  computed: {
    ...mapState({
      actInfos: state => state.retailactivity.actInfos
    })
  },
  methods: {
    ...mapMutations({
      setActivityInfos: 'setActivityInfos',
      actInfoFilled: 'actInfoFilled'
    })
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px 20px;
  }

  .warn {
    margin-left: 20px;
    color: #bbb;
  }
}
</style>
