<template>
  <div>
    <el-form :inline="true">
      <el-form-item label="商品编号">
        <el-input placeholder="请输入商品编号" v-model="params.goodsId"></el-input>
      </el-form-item>
      <el-form-item label="SPU货号">
        <el-input placeholder="请输入SPU货号" v-model="params.spu"></el-input>
      </el-form-item>
      <el-form-item label="商品名称">
        <el-input placeholder="请输入商品名称" v-model="params.goodsName"></el-input>
      </el-form-item>
      <el-form-item label="状态">
        <el-select placeholder="请选择商品状态" v-model="params.status">
          <el-option v-for="state in supplierStatus" :key="state.id" :label="state.name" :value="state.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item style="float: right;">
        <el-button type="primary" icon="el-icon-search" @click="searchSupplier">搜索</el-button>
        <el-button type="primary" icon="el-icon-refresh" @click="clear">清空</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import { RETAIL_ACTIVITY_STATUS } from '../../static'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'ActSupplierSearch',
  data() {
    return {
      supplierStatus: [{ id: 0, name: '已停用' }, { id: 1, name: '启用' }],
      params: {
        goodsId: '',
        spu: '',
        goodsName: '',
        status: ''
      }
    }
  },
  computed: {
    ...mapState({
      actInfos: state => state.retailactivity.actInfos
    })
  },
  methods: {
    searchSupplier() {
      console.log(this.params)
      this.$api.get(`/mlm/activity/${this.actInfos.actId}/goods/list`, this.params, res => {
        if (res.code === 1) {
          console.log(res)
          this.updateGoodsList(res.data.mlmActivityGoodsList)
        }
      })
    },
    clear() {
      let params = this.params
      for (let key in params) {
        params[key] = ''
      }
    },
    ...mapMutations({
      updateGoodsList: 'updateGoodsList'
    })
  }
}
</script>

<style lang="scss" scoped></style>
