<template>
  <div>
    <section>
      <h2>活动基础信息</h2>
      <el-row :gutter="20">
        <el-col :span="6">活动ID：{{ actInfos.actId }}</el-col>
        <el-col :span="6">活动描述：{{ actInfos.actDesc }}</el-col>
        <el-col :span="6">
          <h2>
            <i>活动状态：{{ status }}</i>
          </h2>
        </el-col>
        <el-col :span="6" v-if="show_operate">
          <template v-if="activity_state === 0">
            <el-button type="primary" @click="agreement">通过</el-button>
            <el-button class="refuse" @click="refuse">拒绝</el-button>
          </template>
          <template v-else-if="activity_state === 5 || activity_state === 6">
            <el-button type="primary" @click="exitact">编辑</el-button>
            <el-button class="refuse" @click="stopact">停用</el-button>
          </template>
          <template v-else-if="activity_state === 3">
            <p>操作员：{{ appr_infos.approver }}</p>
            <p>
              停用时间：<template v-if="appr_infos.disableTime">{{
                utils.formatDate('YYYY-MM-DD HH:mm:ss', appr_infos.disableTime * 1000)
              }}</template>
            </p>
            <p>停用原因：{{ appr_infos.disableReason }}</p>
          </template>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">活动名称：{{ actInfos.actName }}</el-col>
        <el-col :span="6">SPU数量：{{ actInfos.spuQuantity }}</el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">申请人：{{ appr_infos.applicant }}</el-col>
        <el-col :span="6">审批人：{{ appr_infos.approver }}</el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6"
          >提交时间：
          <template v-if="appr_infos.createdAt">{{
            utils.formatDate('YYYY-MM-DD HH:mm:ss', appr_infos.createdAt * 1000)
          }}</template>
        </el-col>
        <el-col :span="6"
          >开始时间：
          <template v-if="appr_infos.startTime">{{
            utils.formatDate('YYYY-MM-DD HH:mm:ss', appr_infos.startTime)
          }}</template>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="6">
          审核时间：
          <template v-if="appr_infos.approvalTime">{{
            utils.formatDate('YYYY-MM-DD HH:mm:ss', appr_infos.approvalTime * 1000)
          }}</template>
        </el-col>
        <el-col :span="6"
          >结束时间：
          <template v-if="appr_infos.endTime">{{
            utils.formatDate('YYYY-MM-DD HH:mm:ss', appr_infos.endTime)
          }}</template>
        </el-col>
      </el-row>
    </section>

    <el-dialog title="活动信息编辑" :visible.sync="dialogTableVisible">
      <v-info></v-info>
      <div slot="footer">
        <el-button type="primary" @click="saveInfos">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { RETAIL_ACTIVITY_STATUS } from '../../static'
import FillinActInfo from './FillinActInfo'
export default {
  name: 'WaitVerify',
  // props: ['spucount', 'committime'],
  data() {
    return {
      show_operate: true,
      startTime: '',
      endTime: '',
      click_exit: true,
      dialogTableVisible: false
    }
  },
  components: { 'v-info': FillinActInfo },
  mounted() {
    this.startTime = this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.actInfos.startTime)
    this.endTime = this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.actInfos.endTime)
  },
  watch: {
    activity_state(val) {
      // 已结束和已拒绝的状态没有按钮
      if (val === 2 || val === 7) {
        this.show_operate = false
      }
    }
  },
  computed: {
    ...mapState({
      actInfos: state => state.retailactivity.actInfos,
      activity_state: state => state.retailactivity.activity_state,
      stop_reason: state => state.retailactivity.stop_reason,
      stop_date: state => state.retailactivity.stop_date,
      appr_infos: state => state.retailactivity.appr_infos,
      successcount: state => state.retailactivity.successcount,
      committime: state => state.retailactivity.committime,
      params_info: state => state.retailactivity.params_info
    }),
    status() {
      let statusName = ''
      // 使用vuex getters改进
      if (this.activity_state && typeof this.activity_state !== 'object') {
        let s_lists = RETAIL_ACTIVITY_STATUS
        for (let i = 0; i < s_lists.length; i++) {
          if (this.activity_state === s_lists[i].id) {
            statusName = s_lists[i].name
            break
          }
        }
      }
      return statusName
    }
  },
  methods: {
    ...mapMutations({
      setActivityInfos: 'setActivityInfos',
      actStateChange: 'actStateChange',
      showAgreeDialog: 'showAgreeDialog',
      showStopDialog: 'showStopDialog',
      setApprInfos: 'setApprInfos'
    }),
    // 通过，弹出提示框
    agreement() {
      this.showAgreeDialog()
    },
    refuse() {
      this.$api.put('/mlm/activity/' + this.actInfos.actId + '/check?isCheck=2', null, res => {
        console.log(res)
        if (res.code === 1) {
          this.actStateChange(res.data.isCheck)
          this.setApprInfos(res.data)
        }
      })
    },
    exitact() {
      this.dialogTableVisible = true
      this.actInfos.startTime = new Date(this.actInfos.startTime)
      this.actInfos.endTime = new Date(this.actInfos.endTime)
      // console.log(this.actInfos.startTime)
      // this.actInfos.startTime = thi
    },
    saveInfos() {
      this.setActivityInfos(this.actInfos)
      this.$api.put('/mlm/activity', this.params_info, res => {
        if (res.code === 1) {
          // console.log(res);
          this.dialogTableVisible = false
        }
      })
    },
    stopact() {
      this.showStopDialog()
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px 20px;
  }

  .warn {
    margin-left: 20px;
    color: #bbb;
  }
}
</style>
