<template>
  <div class="container">
    <page-title first-title="分销活动管理"></page-title>
    <!-- 活动基础信息 -->
    <v-fill-act-info v-if="fill_in_info"></v-fill-act-info>
    <!-- 待审核活动 -->
    <v-wait-verify v-else></v-wait-verify>
    <!-- 为了测试，这行才是大爷 -->
    <!-- <v-wait-verify></v-wait-verify> -->

    <!-- 批量导入商品 -->
    <section>
      <div>
        <h2>
          <span>批量导入商品</span>
          <span class="down-temp" v-if="!import_flag" @click="downtemplate">模板下载</span>
          <span class="down-temp" v-else @click="exporttemplate">导出</span>
        </h2>
        <template v-if="import_flag">
          <!-- 当提交审核后才出现搜索框 -->
          <v-act-search v-if="isWaitVerify"></v-act-search>
          <el-table
            :data="act_supplier_lists"
            stripe
            style="width: 100%"
            :header-cell-style="{ background: '#eef1f6' }"
          >
            <el-table-column prop="goodsId" label="商品编号"></el-table-column>
            <el-table-column prop="spu" label="SPU货号"></el-table-column>
            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
            <el-table-column label="本店售价/积分价">
              <template slot-scope="scope">
                <span>{{ scope.row.ourShopPrice }}</span> /
                <span>{{ scope.row.ourShopCoin }}</span>
              </template>
            </el-table-column>
            <el-table-column label="分销底价/积分价">
              <template slot-scope="scope">
                <span>{{ scope.row.realShopPrice }}</span> /
                <span>{{ scope.row.distributionCoin }}</span>
              </template>
            </el-table-column>
            <el-table-column label="+￥+Ⓗ">
              <template slot-scope="scope">
                <span>￥{{ scope.row.differencePrice }}</span> /
                <span>Ⓗ{{ scope.row.differenceCoin }}</span>
              </template>
            </el-table-column>
            <el-table-column label="运营底价/积分价">
              <template slot-scope="scope">
                <span>{{ scope.row.shopPrice }}</span> /
                <span>{{ scope.row.operationCoin }}</span>
              </template>
            </el-table-column>
            <el-table-column label="活动底价/积分价">
              <template slot-scope="scope">
                <span>{{ scope.row.actOperationPrice }}</span> /
                <span>{{ scope.row.actOperationCoin }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="newcomerCommission" label="拉新奖励"></el-table-column>
            <el-table-column prop="actCommission" label="活动奖励"></el-table-column>
            <el-table-column prop="statusName" label="状态">
              <!-- <template slot-scope="scope">
                <span v-if="cur_good_id.indexOf(scope.row.goodsId) > -1">已停用</span>
                <span v-else>启用</span>
              </template>-->
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope" v-if="scope.row.status === 1">
                <el-button
                  @click="stopThisSupplier(scope.row.goodsId, scope.row.goodsName, scope.row.spu)"
                  type="text"
                  size="small"
                  style="color: #f00"
                  >停用</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </template>
        <div v-if="!isWaitVerify">
          <div class="add-wrapper">
            <p class="add">
              <el-upload
                :action="action"
                :data="params_info"
                :on-success="importSuccess"
                :show-file-list="false"
                :disabled="!info_flag"
                :with-credentials="true"
                >+ {{ importSuppliers.add_content }}</el-upload
              >
            </p>
          </div>
          <div class="btn-wrapper">
            <el-button @click="save()" :type="importSuppliers.type" :disabled="!(import_flag && info_flag)">{{
              importSuppliers.btn_content
            }}</el-button>
            <el-button @click="cancel()">取消</el-button>
          </div>
        </div>
      </div>
    </section>

    <div class="self_dialog">
      <!-- 导入商品确认对话框 -->
      <el-dialog :visible.sync="centerDialogVisible" width="50%" :show-close="false">
        <div slot="title">导入商品确认</div>
        <div>
          <div class="main-title">
            <p>
              本次共导入
              <span>{{ res_data.totalCount }}</span
              >件SPU数据
            </p>
            <p>
              其中成功
              <span>{{ successcount }}</span
              >件，失败 <span>{{ res_data.failCount }}</span
              >件
            </p>
            <p>请仔细核对商品信息后重新上传失败数据</p>
          </div>
          <div class="fail_info">
            <p>失败数据如下：</p>
            <p v-for="failgood in res_data.failInfos" :key="failgood.failGoodsNum">
              <span>商品编码：{{ failgood.failGoodsNum }}</span>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <span>失败提示：{{ failgood.failMsg }}</span>
            </p>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <!-- <el-button @click="centerDialogVisible = false">取 消</el-button> -->
          <el-button type="primary" @click="confirmCallBack">确 定</el-button>
        </div>
      </el-dialog>

      <!-- 审核通过对话框 -->
      <el-dialog :visible.sync="agreeDialogVisible" width="50%" center :show-close="false">
        <div slot="title">审核通过确认</div>
        <div>
          <p>
            确定要通过
            <label>{{ actInfos.actName }}</label
            >活动吗
          </p>
          <p>
            该活动包含
            <span class="color-01">{{ actInfos.spuQuantity }}</span
            >个SPU
          </p>
          <p>活动通过后，在相应时段中，活动中的全部商品的价格将会刷新，请慎重操作</p>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="verifyOperate(1)">通过</el-button>
          <el-button class="refuse" @click="verifyOperate(2)">拒绝</el-button>
        </div>
      </el-dialog>
      <!-- 停用活动对话框 -->
      <el-dialog :visible.sync="stopDialogVisible" width="50%" center :show-close="false">
        <div slot="title">活动停用确认</div>
        <div>
          <p>
            确定要停用
            <label>{{ actInfos.actName }}</label
            >活动吗
          </p>
          <p>活动停用后，将不能再次编辑并回复，请慎重操作</p>
        </div>
        <div>
          <p style="text-align: left"><label>*</label>请输入停用理由（不少于10个汉字）</p>
          <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="stop_reason"></el-input>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="stopCancel">取消</el-button>
          <el-button class="refuse" @click="stopActivity">停用</el-button>
        </div>
      </el-dialog>
      <!-- 停用商品 -->
      <el-dialog :visible.sync="stopGoodsDialog" width="50%" center :show-close="false">
        <div slot="title">商品停用确认</div>
        <div>
          <p>确定要停用以下SPU商品分销价格吗？</p>
          <p>
            商品名称：
            <span>{{ cur_stop_good_name }}</span>
          </p>
          <p>
            SPU货号：
            <span>{{ cur_stop_spu }}</span>
          </p>
          <p>
            该操作不影响该活动中的其他SPU商品的分销价格。停用后不能再次启用，请调整该商品的分销价格设置后，重新上传该商品
          </p>
          <p>停用后，该SPU商品将沿用初始设置的分销价格</p>
        </div>

        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="stopGoodsCancel">取消</el-button>
          <el-button class="refuse" @click="stopGoods">停用</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import ActSupplierSearch from './child/ActSupplierSearch'
import FillinActInfo from './child/FillinActInfo'
import WaitVerify from './child/WaitVerify'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'RetailActivityAdd',
  data() {
    return {
      action: process.env.VUE_APP_JAVASERVER_HOST + '/mlm/activity/goods/list/excel',
      actId: this.$route.params.actId ? this.$route.params.actId : '',
      fill_in_info: true, // 活动基础信息填写
      import_flag: 0, // 商品是否导入标志
      stopGoodsDialog: false,
      centerDialogVisible: false, // 对话框是否显示
      act_supplier_lists: [],
      isWaitVerify: false,
      cur_stop_good: '',
      cur_stop_good_name: '',
      cur_stop_spu: '',
      // isStop: false, // 活动商品是否停用
      cur_good_id: [], // 点击了停用的商品id
      res_data: {
        failInfos: [],
        totalCount: 0,
        failCount: 0,
        successCount: 0
      }
    }
  },
  mounted() {
    console.log(this.$route.params.actId)
    let act_id = this.$route.params.actId
    if (act_id) {
      this.fill_in_info = false
      this.$api.get(`/mlm/activity/${act_id}`, null, res => {
        if (res.code === 1) {
          console.log(res)
          this.import_flag = true
          this.isWaitVerify = true
          // 时间格式化
          res.data.mlmActivityVo.startTime *= 1000
          res.data.mlmActivityVo.endTime *= 1000
          this.setApprInfos(res.data.mlmActivityVo)
          this.setActivityInfos(res.data.mlmActivityVo)
          this.actStateChange(res.data.mlmActivityVo.isCheck)
          this.updateGoodsList(res.data.mlmActivityGoodsList)
          this.act_supplier_lists = res.data.mlmActivityGoodsList
        }
      })
    }
  },
  components: {
    'v-act-search': ActSupplierSearch,
    'v-fill-act-info': FillinActInfo,
    'v-wait-verify': WaitVerify
  },
  watch: {
    goods_lists: {
      handler() {
        this.act_supplier_lists = this.goods_lists
      },
      deep: true
    }
  },
  computed: {
    importSuppliers() {
      if (this.import_flag) {
        return {
          add_content: '继续导入商品，新数据将会覆盖原数据',
          type: 'primary',
          btn_content: '保存并提审'
        }
      }
      return {
        add_content: '批量导入商品（活动信息填写完毕方可导入）',
        type: 'info',
        btn_content: '保存'
      }
    },
    stop_reason: {
      get() {
        return this.$store.state.retailactivity.stop_reason
      },
      set(v) {
        this.$store.commit('setStopReason', v)
      }
    },
    ...mapState({
      info_flag: state => state.retailactivity.info_flag,
      params_info: state => state.retailactivity.params_info,
      actInfos: state => state.retailactivity.actInfos,
      agreeDialogVisible: state => state.retailactivity.agreeDialogVisible,
      stopDialogVisible: state => state.retailactivity.stopDialogVisible,
      appr_infos: state => state.retailactivity.appr_infos,
      goods_lists: state => state.retailactivity.goods_lists,
      committime: state => state.retailactivity.committime,
      successcount: state => state.retailactivity.successcount
    })
  },
  methods: {
    importSuccess(response) {
      console.log('import success', response)
      if (response.code === 1) {
        // 导入成功，弹框提示
        this.res_data = response.data.templates
        this.act_supplier_lists = response.data.mlmActivityGoodsPage.mlmActivityGoodsList
        this.actInfos.actId = response.data.templates.actId
        this.setActivityInfos(this.actInfos)
        this.centerDialogVisible = true
        this.updateGoodsList(response.data.mlmActivityGoodsPage.mlmActivityGoodsList)
        this.setSpuCount(response.data.templates.successCount)
      }
    },
    // Dialog Confirm_Callback
    confirmCallBack() {
      this.centerDialogVisible = false
      this.import_flag = 1
    },
    // 保存
    save() {
      // 保存成功，执行回调
      this.$api.post('/mlm/activity/', this.params_info, res => {
        if (res.code === 1) {
          this.$alert('活动保存成功', '提示信息', {
            confirmButtonText: '确定',
            callback: () => {
              this.isWaitVerify = true
              this.fill_in_info = false
              // 存储活动状态为待审核
              this.actStateChange(0)
              this.setCommitTime(this.utils.formatDate('YYYY-MM-DD HH:mm:ss', new Date()))
              this.setApprInfos(res.data)
            }
          })
        }
      })
    },
    // 取消，返回上级
    cancel() {
      // 取消要删除活动id,如果已经导入商品，那么已经在后台生成活动id，此时取消，需要调后台删除活动接口，否则会产生垃圾数据
      if (this.import_flag) {
        this.$api.delete('/mlm/activity/' + this.actInfos.actId, null, res => {
          if (res.code === 1) {
            this.$router.go(-1)
          }
        })
      } else {
        this.$router.go(-1)
      }
    },
    verifyOperate(flag) {
      this.$api.put('/mlm/activity/' + this.actInfos.actId + '/check?isCheck=' + flag, null, res => {
        console.log(res)
        if (res.code === 1) {
          this.actStateChange(res.data.isCheck)
          this.setApprInfos(res.data)
        }
        this.showAgreeDialog()
      })
    },
    stopCancel() {
      this.showStopDialog()
    },
    stopActivity() {
      if (!this.stop_reason) {
        this.$message.error('停用理由必填')
        return
      }
      /* this.actStateChange(3)
      this.setStopDate(new Date())
      this.showStopDialog() */
      this.$api.put(`/mlm/activity/${this.actInfos.actId}/stop?disableReason=${this.stop_reason}`, null, res => {
        if (res.code === 1) {
          this.$alert('活动已停用', '提示信息', {
            confirmButtonText: '确定',
            callback: () => {
              // 存储活动状态为待审核
              this.actStateChange(res.data.isCheck)
              this.setStopDate(this.utils.formatDate('YYYY-MM-DD HH:mm:ss', new Date()))
              this.showStopDialog()
            }
          })
        }
      })
    },
    // 停用商品
    stopThisSupplier(goodsId, goodsName, spu) {
      this.cur_stop_good = goodsId
      this.cur_stop_good_name = goodsName
      this.cur_stop_spu = spu
      this.stopGoodsDialog = true
    },
    stopGoodsCancel() {
      this.stopGoodsDialog = false
    },
    stopGoods() {
      this.$api.put(`/mlm/activity/${this.actInfos.actId}/goods/${this.cur_stop_good}/stop`, null, res => {
        console.log(res)
        if (res.code === 1) {
          this.stopGoodsDialog = false
          window.location.reload()
        }
      })
    },
    downtemplate() {
      //导出下载excel
      let a = document.createElement('a')
      a.href = process.env.VUE_APP_JAVASERVER_HOST + '/mlm/goods/template'
      a.click()
      // downTemplate().then(res => {
      //   // 下载成功
      // })
    },
    exporttemplate() {
      let a = document.createElement('a')
      a.href = `${process.env.VUE_APP_JAVASERVER_HOST}/mlm/activity/${this.actId}/goods/list/excel`
      a.click()
      // exportTemplate().then(res => {
      //   // 导出成功
      // })
    },
    ...mapMutations({
      setActivityInfos: 'setActivityInfos',
      actStateChange: 'actStateChange',
      showAgreeDialog: 'showAgreeDialog',
      showStopDialog: 'showStopDialog',
      setStopDate: 'setStopDate',
      setApprInfos: 'setApprInfos',
      updateGoodsList: 'updateGoodsList',
      setCommitTime: 'setCommitTime',
      setSpuCount: 'setSpuCount'
    })
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px 20px;
  }

  .warn {
    margin-left: 20px;
    color: #bbb;
  }
}

.main-title {
  p {
    font-weight: bold;
  }
  span {
    color: #f00;
  }
}

.down-temp {
  float: right;
  color: #41a5e1;
  cursor: pointer;
  font-weight: normal;
  font-size: 0.9em;
}

.add-wrapper {
  width: 100%;
  height: 70px;
  margin-bottom: 30px;
  .add {
    line-height: 70px;
    text-align: center;
    border: 1px dashed #bbb;
    margin: 10px;
    cursor: pointer;
  }
}

.btn-wrapper {
  display: flex;
  justify-content: center;

  .el-button {
    padding: 8px 35px;
  }
}

.color-01 {
  color: #f00;
}
</style>

<style lang="scss">
.refuse {
  color: #f00;
  border-color: #f00;
}
.self_dialog {
  .el-dialog__header {
    background: #505763;
    text-align: left;
    padding: 5px 20px;
    color: #fff;
    height: 26px;
    line-height: 26px;
    font-size: 14px;
  }
  .el-dialog__body {
    text-align: center;
    .fail_info {
      text-align: left;
    }
  }
  .el-dialog__footer {
    background: #e4e4e4;
    text-align: center;
    padding: 5px 20px;
    .el-button {
      padding: 5px 32px;
    }
  }
  label {
    color: #f00;
  }
}
</style>
