<template>
  <div class="container">
    <page-title first-title="分销活动"></page-title>
    <div class="button-wrapper">
      <el-button type="primary" @click="activityAdd()">创建活动</el-button>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="活动ID">
          <el-input v-model="params.actId" placeholder="活动ID"></el-input>
        </el-form-item>
        <el-form-item label="活动名称">
          <el-input v-model="params.actName" placeholder="活动名称"></el-input>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker v-model="params.startTime" type="datetime" placeholder="开始时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="结束时间">
          <el-date-picker v-model="params.endTime" type="datetime" placeholder="结束时间"></el-date-picker>
        </el-form-item>
        <el-form-item label="申请人">
          <el-input v-model="params.applicant" placeholder="申请人"></el-input>
        </el-form-item>
        <el-form-item label="审批人">
          <el-input v-model="params.approver" placeholder="审批人"></el-input>
        </el-form-item>
        <el-form-item label="活动状态">
          <el-select v-model="params.isCheck" placeholder="活动状态">
            <el-option
              v-for="status in activityStateList"
              :key="status.id"
              :label="status.name"
              :value="status.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getActivityList()" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="retailactivity_list" stripe style="width: 100%" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="actId" label="活动ID"></el-table-column>
        <el-table-column prop="actName" label="活动名称"></el-table-column>
        <el-table-column prop="applicant" label="申请人"></el-table-column>
        <el-table-column prop="approver" label="审批人"></el-table-column>
        <el-table-column prop="spuQuantity" label="SPU数量"></el-table-column>
        <el-table-column prop="startTime" label="开始时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="endTime" label="结束时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="isCheckName" label="当前状态"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="activityInfos(scope.row.actId)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 时间紧迫，分页暂时不做 -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.pageSize"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { RETAIL_ACTIVITY_STATUS } from '../static'
import { mapMutations } from 'vuex'
export default {
  name: 'RetailActivityList',
  data() {
    return {
      retailactivity_list: [],
      currentPage: 1,
      params: {
        pageNumber: 1,
        pageSize: 10,
        actId: '',
        actName: '',
        startTime: '',
        endTime: '',
        applicant: '',
        approver: '',
        isCheck: ''
      },
      total: 0,
      activityStateList: RETAIL_ACTIVITY_STATUS
    }
  },
  created() {
    this.getActivityList()
  },
  methods: {
    ...mapMutations({
      setActivityInfos: 'setActivityInfos'
    }),
    getActivityList() {
      let startT = this.params.startTime
      let endT = this.params.endTime
      startT ? (this.params.startTime = startT.getTime() / 1000) : (this.params.startTime = '')
      endT ? (this.params.endTime = endT.getTime() / 1000) : (this.params.endTime = '')
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('/mlm/activity/list?' + pramstr, null, res => {
        if (res.code === 1) {
          this.retailactivity_list = res.data.mlmActivityList
          this.total = res.data.totalElements
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.pageNumber = currentPage
      this.getActivityList()
    },
    // 活动新增
    activityAdd() {
      this.setActivityInfos({
        actId: '',
        actName: '',
        actDesc: '',
        startTime: '',
        endTime: ''
      })
      this.$router.push({ name: 'RetailActivityAdd', params: {} })
    },
    // 清空搜索框
    clear() {
      // console.log(Object.values(this.params));
      this.params.pageNumber = 1
      this.params.pageSize = 10
      let _params = this.params
      _params.actId = ''
      _params.actName = ''
      _params.startTime = ''
      _params.endTime = ''
      _params.approver = ''
      _params.applicant = ''
      _params.isCheck = ''
    },
    activityInfos(id) {
      // console.log(row)
      this.$router.push({ name: 'RetailActivityAdd', params: { actId: id } })
    },
    dateFormat(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : '长期有效'
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  text-align: right;
}
.search-wrapper {
  margin-top: 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
